<template>
  <v-app>
    <v-main style="background-color: #111722" class="pt-4">
      <section id="Singlemovie">
        <section id="Movie" class="pb-12">
          <!-- START::__MOVIES_PRODUCTS__ -->
          <v-container>
            <v-row>
              <v-col>
                <div class="main_heading mb-12">
                  <h1>
                    <span>Search Result</span>
                  </h1>
                  <span class="special_design">
                    <i></i>
                    <i></i>
                    <i></i>
                  </span>
                </div>
              </v-col>
            </v-row>
            <v-divider style="border-color: #8080802e"></v-divider>

            <v-row v-if="$store.state.movie.isImageLoaded">
              <!-- START::__MOVIES_PRODUCTS__::CARD__ -->
              <v-col
                cols="6"
                sm="4"
                md="3"
                lg="2"
                v-for="(movie, i) in S_searchResult"
                :key="i"
                class="my-5"
                v-show="movie.poster_path !== null"
              >
                <v-hover v-slot="{ hover }" open-delay="50">
                  <v-card
                    class="mx-auto"
                    v-if="movie.poster_path !== null"
                    @click="
                      saveToLocal(movie);
                      redirectTo(movie.id);
                    "
                    style="cursor: pointer"
                    max-width="200"
                    max-height="250"
                  >
                    <v-img
                      :src="`https://image.tmdb.org/t/p/w500${movie.poster_path}`"
                      :min-width="C_img.width"
                      :min-height="C_img.height"
                    >
                      <template #placeholder>
                        <v-card color="#111722">
                          <!-- <v-slide-x-transition> -->
                          <v-skeleton-loader
                            type="image, list-item-three-line"
                            min-height="300"
                            :loading="true"
                            height="300"
                            v-bind="attrs"
                          />
                          <!-- </v-slide-x-transition> -->
                        </v-card>
                      </template>
                      <div
                        class="d-flex flex-column justify-space-between"
                        style="height: 100%"
                      >
                        <v-card-text
                          class="py-2 px-1 d-flex justify-space-between"
                        >
                          <h4
                            class="caption rating_box white--text font-weight-light px-4 py-0"
                          >
                            WEB-DL
                          </h4>
                          <div
                            class="rating rating_box white--text font-weight-light px-2 py-0"
                          >
                            <v-icon left small color="#ffab31">mdi-star</v-icon>
                            <span class="caption">{{
                              movie.vote_average
                            }}</span>
                          </div>
                        </v-card-text>
                        <!-- START::HOVER -->
                        <v-slide-y-reverse-transition>
                          <v-card v-if="hover" class="background transparent">
                            <v-card-text class="text-center">
                              <v-btn
                                small
                                color="#ffab31"
                                dark
                                @click="
                                  saveToLocal(movie);
                                  redirectTo(movie.id);
                                "
                                style="cursor: pointer"
                              >
                                <v-icon>mdi-play-outline</v-icon>
                                <span class="text-capitalize d-block"
                                  >watch</span
                                >
                              </v-btn>
                            </v-card-text>
                            <v-divider
                              class="pb-1 pt-3 divider_animation"
                              style="border-color: rgb(255 255 255 / 25%)"
                            ></v-divider>
                            <v-card-subtitle
                              :to="{
                                name: 'movieproduct',
                                params: { item: movie.id },
                              }"
                              @click="saveToLocal(movie)"
                              style="cursor: pointer"
                              class="caption white--text text-center pt-0"
                              >{{ movie.title }}</v-card-subtitle
                            >
                            <v-card-text
                              class="d-flex align-center justify-center px-2"
                            >
                              <div
                                class="parent_chip"
                                style="display: flex; gap: 2px; flex-wrap: wrap"
                              >
                                <div
                                  v-for="(mov, ind) in movie.genre_ids"
                                  :key="ind"
                                >
                                  <v-chip
                                    small
                                    v-show="item.id == mov && ind < 2"
                                    v-for="(item, i) in chips"
                                    :color="
                                      item.id == 28
                                        ? 'error'
                                        : item.id == 35
                                        ? 'yellow'
                                        : item.id == 16
                                        ? 'indigo'
                                        : ''
                                    "
                                    :key="i"
                                    :class="[
                                      { 'white--text': item.id == 16 },
                                      i == 0 ? 'hidden-sm-and-down' : '',
                                    ]"
                                  >
                                    {{ item.name }}
                                  </v-chip>
                                </div>
                              </div>
                            </v-card-text>
                          </v-card>
                        </v-slide-y-reverse-transition>
                        <!-- END::HOVER -->
                      </div>
                    </v-img>
                  </v-card>
                </v-hover>
              </v-col>
              <!-- START::__MOVIES_PRODUCTS__::CARD__ -->
            </v-row>
            <div v-else style="width: 100px; height: 1570px"></div>
          </v-container>
        </section>
        <!--  -->
        <section id="Notfound" v-if="S_searchResult.length == 0">
          <v-container>
            <h1 class="text-center text-h2 white--text">Not Found!</h1>
          </v-container>
        </section>
      </section>
    </v-main>
  </v-app>
</template>

<script>
import axios from "axios";
import { mapActions, mapState } from "vuex";
// import api from "@/components/Movie/api/movie.js";
export default {
  props: ["id"],
  data: () => ({
    loading: false,
    firstLoad: true,
    isImageLoaded: true,
    attrs: {
      class: "mb-6",
      elevation: 2,
      boilerplote: false,
    },
    chips: [],
    text: "",
    apiKey: "194d8a67379374e13e12c99420c19098",
    searchResult: [],
  }),
  mounted() {
    // this.isImageLoaded = true;
    this.text = this.$route.params.id;
    // console.log(this.S_skeletonLoading);
    this.getGenres();
  },
  computed: {
    newText() {
      return this.$route.params.id;
    },
    ...mapState({
      S_searchResult: (store) => store.movie.S_searchResult,
      S_skeletonLoading: (store) => store.movie.S_skeletonLoading,
    }),
     C_img() {
      return this.$vuetify.breakpoint.lgAndUp
        ? { width: 173, height: 300 }
        : this.$vuetify.breakpoint.mdOnly
        ? { width: 200, height: 300 }
        : this.$vuetify.breakpoint.mdOnly
        ? { width: 175, height: 260 }
        : { width: 155, height: 230 };
    },
  },
  methods: {
    redirectTo(movieId) {
      this.$router
        .push({ name: "movieproduct", params: { item: movieId } })
        .then(() => {
          this.$vuetify.goTo(0, {
            duration: 750,
          });
        })
        .catch((err) => err);
    },
    async fetchSearch() {
      await axios
        .get(
          `https://api.themoviedb.org/3/search/movie?query=${this.newText}&api_key=${this.apiKey}`
        )
        .then((resp) => {
          // console.log(resp);
          this.searchResult = resp.data.results;
        });
    },
    saveToLocal(item) {
      localStorage.setItem("currentItem", JSON.stringify(item));
      // console.log("hello");
    },
    skeletonLoading() {
      this.firstLoad = true;
      setTimeout(() => {
        this.firstLoad = false;
      }, 2000);
    },
    getGenres() {
      this.$MAIN_API.genres().then((resp) => {
        this.chips = resp.data.genres;
      });
    },
    ...mapActions(["A_changeSkeletonLoading"]),
  },
  created() {
    const readyHandler = () => {
      if (document.readyState == "complete") {
        document.removeEventListener("readystatechange", readyHandler);
        setTimeout(() => {
          this.A_changeSkeletonLoading(false);
        }, 1500);
      }
    };
    document.addEventListener("readystatechange", readyHandler);
    readyHandler();
  },
};
</script>

